@use 'variables';

@mixin telefono{
    @media(min-width:480px) {
        @content;
    }
}
@mixin tablet{
    @media(min-width:768px) {
        @content;
    }
}
@mixin escritorio{
    @media(min-width:992px) {
        @content;
    }
}
@mixin escritorioXL{
    @media(min-width:1200px) {
        @content;
    }
}
