.Animacion1 {
  filter: saturate(200%) opacity(80%);
  transition: ease-out 0.5s;
}
.Animacion1:hover {
  -webkit-transform: scale(1);
  filter: saturate(90%) opacity(120%);
}

.Animacion2 {
  filter: grayscale(70%);
  transition: ease-out 0.5s;
}
.Animacion2:hover {
  filter: grayscale(0%) saturate(150%);
}

html {
  background-image: url(../img/Fondo.png);
  min-height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  box-sizing: border-box;
  font-size: 62.5%;
  /** 1rem = 10px **/
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 2;
}

/* Configuraciones Globales*/
h1, h2, h3, h4 {
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  color: #9cbc3c;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 11.5rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.4rem;
}

h4 {
  font-size: 1.6rem;
}

p {
  font-size: 1.5rem;
  margin: 0;
  padding: 0;
}

li {
  font-size: 1.2rem;
  text-align: justify;
}

img {
  max-width: 100%;
}

a {
  font-size: 2rem;
  text-decoration: none;
}

span {
  font-weight: 900;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  /* 1 */
  display: table;
  /* 2 */
}

.clearfix:after {
  clear: both;
}

@media (min-width: 768px) {
  h2 {
    font-size: 3.5rem;
  }

  h3 {
    font-size: 3rem;
  }

  h4 {
    font-size: 2.3rem;
  }

  p {
    font-size: 1.8rem;
  }

  li {
    font-size: 1.7rem;
  }

  a {
    font-size: 2.5rem;
  }
}
@media (min-width: 992px) {
  h2 {
    font-size: 4rem;
  }

  h3 {
    font-size: 3.5rem;
  }

  h4 {
    font-size: 3rem;
  }

  p {
    font-size: 2rem;
  }

  li {
    font-size: 1.8rem;
  }

  a {
    font-size: 3rem;
  }
}
/*Header*/
.navegacion {
  width: 100%;
  background-color: rgba(255, 255, 255, 0.7);
  box-shadow: 1rem 1rem 10rem 0 rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  z-index: 100;
  transition: ease-in-out 0.5s;
  height: 13rem;
  max-height: 169rem;
}

.logo {
  width: 90%;
  max-width: 190rem;
  height: 100%;
  float: left;
}
.logo img {
  width: 100%;
  min-width: 10rem;
  max-width: 40rem;
  height: 80%;
  min-height: 10rem;
  max-height: 40rem;
  vertical-align: text-top;
}

.navegacion_texto {
  display: inline-block;
  width: 100%;
  height: 200%;
}
.navegacion_texto a {
  background-color: rgba(255, 255, 255, 0.75);
  width: 100%;
  padding: 0.5rem;
  display: block;
  font-weight: 500;
  color: black;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  margin-right: 1rem;
  cursor: pointer;
  transition: ease-in-out 0.5s;
}
.navegacion_texto a:last-child {
  margin-right: 0;
}
.navegacion_texto a:hover {
  background-color: rgba(245, 245, 245, 0.9);
  color: #779c10;
  background-color: white;
}

.contenido-menu {
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

/*Menu Movil*/
.menu-movil {
  width: 6rem;
}
.menu-movil:hover {
  cursor: pointer;
}
.menu-movil span {
  height: 0.8rem;
  margin-bottom: 0.6rem;
  background-color: #6b851f;
  display: block;
}

@media (min-width: 480px) {
  .logo img {
    vertical-align: top;
    height: 100%;
  }

  .menu-movil {
    padding-top: 50% 0;
  }
}
@media (min-width: 768px) {
  .logo {
    width: 40%;
  }

  .contenido-menu {
    display: none;
  }

  .navegacion_texto {
    background-color: rgba(70, 19, 19, 0);
    width: 60%;
    height: 100%;
    float: right;
    text-align: center;
    display: block !important;
  }
  .navegacion_texto a {
    background-color: rgba(70, 19, 19, 0);
    padding: 1rem 1rem;
    display: inline-block;
    margin-right: 1rem;
    width: auto;
    text-align: center;
  }
  .navegacion_texto a:hover {
    background-color: rgba(255, 255, 255, 0);
  }
}
@media (min-width: 992px) {
  .navegacion_texto {
    height: 100%;
  }
  .navegacion_texto a {
    font-size: 2.5rem;
  }
}
@media (min-width: 1200px) {
  .logo {
    width: 35%;
  }

  .navegacion_texto {
    text-align: right;
    padding-top: 2.5rem;
    width: 65%;
    height: 100%;
  }
  .navegacion_texto a {
    font-size: 2.7rem;
  }
}
