/*Invitados*/
.lista-invitados li {
  padding: 1rem;
  list-style: none;
  float: right;
  width: 100%;
}

.invitado {
  position: relative;
  overflow: hidden;
}
.invitado img {
  width: 100%;
  filter: gray;
  -webkit-filter: grayscale(1);
  transition: all 0.6s ease;
}
.invitado p {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 1.5rem 1rem;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  text-align: center;
  color: #9cbc3c;
  font-weight: 500;
  text-transform: uppercase;
  transition: all 0.6s ease;
}
.invitado:hover img {
  -webkit-transform: scale(1.1);
  -webkit-filter: grayscale(0);
}
.invitado:hover p {
  bottom: -8rem;
  opacity: 0;
}
.invitado .letra_pequena {
  font-size: 1.7rem;
}

.mensaje_3 {
  margin-top: 2rem;
  padding: 2rem;
}

