  padding: 2rem;
  text-align: justify;
}

.snip1361 {
  position: relative;
  overflow: hidden;
}

.snip1361 * {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
}

.figura_cambiada {
  position: absolute;
  top: 150%;
  width: 100%;
  height: 100%;
  background-color: white;
  padding: 1.5rem 2.5rem 6.5rem;
}

.figura_cambiada:before {
  position: absolute;
  content: "";
  z-index: 2;
  bottom: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: -webkit-linear-gradient(top, transparent 0%, #ffffff 100%);
  background-image: linear-gradient(to bottom, transparent 0%, #ffffff 100%);
}

.snip1361 h4,
.snip1361 p {
  margin: 0 0 10rem;
  text-align: center;
}

.snip1361 a {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 2;
}

.snip1361:hover .figura_cambiada,
.snip1361.hover .figura_cambiada {
  top: 8rem;
}

.mensaje_Dist {
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: stretch;
}

/*Segundo efecto*/
.thumbnail {
  width: 94%;
  height: 25rem;
  overflow: hidden;
  position: relative;
  opacity: 0.88;
  backface-visibility: hidden;
  transition: all 0.4s ease-out;
}
.thumbnail .Dist_Img {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}
.thumbnail .Dist_p {
  position: absolute;
  z-index: 2;
  top: 10rem;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  padding: 1rem;
  margin: 0 25%;
  text-align: center;
  color: white;
  font-weight: 300;
  transition: all 0.3s ease-out;
}
.thumbnail:hover {
  backface-visibility: hidden;
  transform: scale(1.1, 1.1);
  opacity: 1;
}
.thumbnail:hover .Dist_p {
  opacity: 0;
}

@media (min-width: 480px) {
  .thumbnail {
    width: 94%;
    height: 35rem;
  }
  .thumbnail .Dist_p {
    top: 17rem;
  }
}
